// Require Cordova plugin : cordova-plugin-camera
<template>
  <v-container text-center>
    <v-form ref="form" v-model="form" class="pa-4 pt-6">
      <v-select
        v-model="select"
        :items="items"
        :rules="[(v) => !!v || 'Une espèce est requise']"
        filled
        color="indigo"
        style="min-height: 96px"
        label="Espèce"
        required
      ></v-select>

      <v-textarea
        v-model="description"
        auto-grow
        filled
        color="indigo"
        label="Description"
        style="min-height: 96px"
        rows="1"
      ></v-textarea>

      <v-text-field
        v-model="imagePath"
        filled
        color="indigo"
        label="Photo"
        style="min-height: 96px"
        :rules="[(v) => !!v || 'Une photo est requise']"
      ></v-text-field>

      <img v-if="imagePath !== ''" :src="imagePath" />
      <v-btn @click="takePicture">{{ $t("consult.takePhoto") }}</v-btn>
    </v-form>
    <v-divider></v-divider>
    <v-card-actions>
      <v-btn
        :disabled="!form"
        :loading="isLoading"
        class="white--text"
        color="success"
        depressed
        @click="sendForm"
        >Envoyer</v-btn
      >
      <v-spacer></v-spacer>
      <v-btn text @click="$refs.form.reset()" color="error">
        Reset
      </v-btn>
    </v-card-actions>
  </v-container>
</template>

<script>
import { nativeAlert } from "../libs/index";

export default {
  name: "report",
  data() {
    return {
      select: null,
      description: "",
      imagePath: "",

      items: ["Espèce 1", "Espèce 2", "Espèce 3", "Espèce 4"],

      form: false,
      isLoading: false,
    };
  },
  methods: {
    // Use the camera plugin to capture image
    takePicture() {
      if (navigator.camera) {
        navigator.camera.getPicture(this.setPicture, this.error, {});
      } else {
        // If the navigator.camera is not available display generic error to the user.
        this.error();
      }
    },
    // Set the picture path in the data of the vue
    // this action will automatically update the view.
    setPicture(imagePath) {
      this.imagePath = imagePath;
    },
    error() {
      nativeAlert(this.$t("error"));
    },
    validate() {
      this.$refs.form.validate();
    },
    reset() {
      this.imagePath = "";
      this.$refs.form.reset();
    },
    sendForm() {
      alert(
        "Espèce : " +
          this.select +
          "\nDescription : " +
          this.description +
          "\nPhoto : " +
          this.imagePath
      );
    },
  },
};
</script>

<style scoped>
img {
  width: 50%;
  display: block;
  margin: auto;
}
</style>
